import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { RadioButtonProps } from 'core/types/radio';

import styles from './styles.scss';

const cd = cssDecorator(styles);

function RadioButton(props: RadioButtonProps): JSX.Element {
  const {
    name,
    label,
    value,
    checked,
    disabled,
    uncontrolled,
    textLimitWithEllipsis,
    labelMaxWidth,
    size = 'default'
  } = props;

  const defaultChecked = uncontrolled ? checked : undefined;
  const isActive = uncontrolled ? undefined : Boolean(checked);
  const maxWidth = textLimitWithEllipsis || labelMaxWidth;

  return (
    // eslint-disable-next-line jsx-a11y/label-has-associated-control
    <label
      data-test-id="radio_button_id"
      className={ cd(
        'radio_button',
        {
          [`${ size }`]: true,
          disabled
        }
      ) }
    >
      <input
        type="radio"
        name={ name }
        value={ value }
        disabled={ disabled }
        defaultChecked={ defaultChecked }
        checked={ isActive }
        readOnly={ !uncontrolled }
      />
      <span className={ cd('radio_button__checkmark', { [`${ size }`]: true }) } />
      <span
        style={ maxWidth ? { maxWidth } : undefined }
        className={ cd('radio_button__label', { limit_with_ellipsis: textLimitWithEllipsis }) }
      >
        { label }
      </span>
    </label>
  );
}

export default RadioButton;
